













# For mode info about the algorithm, check out:
# https://codepen.io/FelixLuciano/pen/PVMrEO?editors=0010

export default
  name: 'atomus'


  props:
    electrons:
      type: Object

      default: ->
        base: false
        config:  []


  data: ->
    diagram: []


  computed:
    layersLength: ->
      @diagram.map (layer) =>
        layer.reduce (a, b) => a + b


  methods:
    clearDiagram: ->
      @diagram = new Array(7)
       .fill()
       .map (_, x) =>
         length = 4.5 - Math.abs(x - 3.5)

         new Array(length).fill(0)


    distributeBase: ->
      return unless @electrons.base

      layer = 0
      sublevel = 0
      bases =
        'He': 2
        'Ne': 10
        'Ar': 18
        'Kr': 36
        'Xe': 54
        'Rn': 86

      electrons = bases[@electrons.base]


      while electrons > 0
        x = layer
        y = sublevel

        while y >= 0 and x <= 6
          maxDecay = 2 + 4 * y
          decay = Math.min electrons, maxDecay

          @diagram[x][y] = decay
          electrons -= decay

          x++
          y--

        if layer is sublevel then layer++
        else sublevel++


    ionize: ->
      for electron in @electrons.config
        [layer, sublayer, charge] = electron.split ','

        layer = Number(layer) - 1
        sublayer = {s: 0, p: 1, d: 2, f: 3}[sublayer]
        charge = Number(charge)

        @$set @diagram[layer], sublayer, charge


  watch:
    electrons: ->
      @clearDiagram()
      @distributeBase()
      @ionize()


  created: ->
    @clearDiagram()
    @distributeBase()
    @ionize()

